import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ViewDepositedChequesComponent } from './components/view-deposited-cheques/view-deposited-cheques.component';
import { RegisterBankDepositComponent } from './register-bank-deposit.component';

const routes: Routes = [
  { path: '', component: RegisterBankDepositComponent },
  { path: 'deposited-cheques/:bankId/:depositeId', component: ViewDepositedChequesComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RegisterBankDepositRoutingModule {}
