
import { Component } from '@angular/core';
import * as _ from 'underscore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { DatePipe } from '@angular/common';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { HeaderService } from '../../../../services/header.service';
import { TransactionHistoryService } from '../transactions-history/transaction-history-services/transaction-history.service';
import { AllocationService } from '../../main-fund/allocation/allocation/allocation-services/allocation.service';
import { CommonService } from '../../../../services/common.service';
import { Router } from '@angular/router';
import { SharedService } from '../../../../services/shared.service';
@Component({
  selector: 'app-register-bank-deposit',
  templateUrl: './register-bank-deposit.component.html',
  styleUrls: ['./register-bank-deposit.component.scss']
})
export class RegisterBankDepositComponent {
  searchText: string = '';
  depositId: string = '';
  sort: number = 1;
  totalRecords: number = 18;
  pageNo: number = 0;
  pagin: number;
  pages: any[] = [];
  depositsList: any[] = [];
  branchList: any[] = [];
  categoryList: any;
  categoryLookupId: number;
  search: string;
  selectedDeviceObj: any;
  dropdownList: any[] = [];
  dropdownMenu: FormGroup;
  All: string = 'All';
  paymentTypeList: any;
  selected;
  endDate: any;
  startDate: any;
  fileItem;
  viewCashDeposit;
  totalAmount;
  depositedAmount;
  constructor(
    private modalService: NgbModal,
    private fb: FormBuilder,
    private _headerService: HeaderService,
    private TransactionHistoryService: TransactionHistoryService,
    private _modalService: NgbModal,
    private _routerService: Router,
    private _sharedService: SharedService,
    private _commonService: CommonService,

  ) {
    this.dropdownList = [
      { name: 'Branch', checked: false },
      { name: 'Register Name', checked: false },
      { name: 'Register No.', checked: false },
      { name: 'Employee', checked: false },
    ];
    this.paymentTypeList = [
      'Cash',
      'Cheque',
    ];
    ////
    this._headerService.setTitle('Register Deposit Banks')
  }

  ngOnInit(): void {
    this.getList();
    this.GetCategoryDropDown();
    this.getBranches();
    this.getEmployees();
    this.getRegisters();
  }

  /**
   * get List of directPaymentCollection
   */
  viewAction(item, content) {
    if (item?.sageBank?.isCash) {
      this.TransactionHistoryService.getRegisterDirectCashDeposit(item?.sageBank?.sageBankId, this._sharedService.getRegister?.registerObj?.id, item?.depositeId).subscribe(response => {
        console.log(response);
        this.viewCashDeposit = response.data?.deposits[0];
        this.depositedAmount = response.data?.totalAmount;
        this._modalService.open(content)
      })
    }
    else {
      this._commonService.NavigateToRoute(`/register-bank-deposit/deposited-cheques/` + item?.sageBank?.sageBankId + '/' + item?.depositeId)
    }
  }
  openFileModal(item, content) {
    let fileType = this.getFileType(item?.file);
    this.fileItem = item;
    this.fileItem['fileType'] = fileType;
    this._modalService.open(content)
  }
  getFileType(fileName) {
    return fileName?.split('.').pop();
  }
  getList() {
    var branches = this.dropDorwnBranch.toString();
    var registerName = this.dropdownRegister.toString();
    var registerNo = this.newDropdownRegisterNo.toString();
    var employee = this.dropDownEmployeeId.toString();
    var payments = this.newDropdownPayment.toString();

    this.TransactionHistoryService.getAllRegisterDirectDeposit(
      this.searchText,
      this.depositId,
      this.sort,
      this.pageNo,
      branches,
      registerName,
      registerNo,
      employee,
      payments,
      this.startDate,
      this.endDate
    ).subscribe((response) => {
      console.log(response.data);
      if (response) {
        this.depositsList = response?.data;
        this.totalAmount = response?.data?.totalAmount;
        this.totalRecords = response?.totalRecordCount;
        this.pagin = Math.ceil(this.totalRecords / 6);
        this.pages = _.range(this.pagin);
      }
    });
  }
  /**
   * get category dropdown list
   */

  GetCategoryDropDown() {
    this.TransactionHistoryService.GetCategory().subscribe((response) => {
      if (response) {
        this.categoryList = response?.data;
      }
    });
  }

  /**
   * getCartegoryId
   */
  changeCategory(id) {
    this.categoryLookupId = id.value;
    this.getList();
  }
  /**
   * get Registers
   */
  registersList: any;
  getRegisters() {
    let params = `&pageNo=0&pageSize=1000`

    this.TransactionHistoryService.GetRegisterName(params).subscribe((response) => {
      if (response) {
        this.registersList = response?.data;
      }
    });
  }

  /**
   * get Branches
   */
  branchesList: any;
  getBranches() {
    let params = `&pageNo=0&pageSize=1000`
    this.TransactionHistoryService.GetBranches(params).subscribe((response) => {
      if (response) {
        this.branchesList = response?.data;
      }
    });
  }

  /**
   * get Branches
   */
  EmployeesList: any;
  getEmployees() {
    let params = `&pageNo=0&pageSize=1000`

    this.TransactionHistoryService.GetEmployees(params).subscribe((response) => {
      if (response) {
        this.EmployeesList = response?.data;
      }
    });
  }

  /**
   * filter selection
   */
  register: boolean = false;
  branch: boolean = false;
  registerNo: boolean = false;
  employee: boolean = false;
  collectionDate: boolean = false;
  customerName: boolean = false;
  paymentType: boolean = false;
  filterRegisterName(event) {
    this.register = event.checked;
  }
  filterRegisterNo(event) {
    this.registerNo = event.checked;
  }
  filterBranch(event) {
    this.branch = event.checked;
  }
  filterEmployee(event) {
    this.employee = event.checked;
  }
  filterCollectionDates(event) {
    this.collectionDate = event.checked;
  }
  filterPaymentType(event) {
    this.paymentType = event.checked;
  }
  lastAction: any;
  newDropdownBranch: any[] = [];
  dropDorwnBranch: any[] = [];
  selectedBranch: any[] = [];
  applyBranch(item, event) {
    const obj = item.branchName;
    const branchId = item.branchId;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownBranch.push(obj); // pushing object to newArray[]
      this.dropDorwnBranch.push(branchId);
    } else {
      this.newDropdownBranch = this.newDropdownBranch.filter((v) => v !== obj);
      this.dropDorwnBranch = this.dropDorwnBranch.filter((x) => x !== branchId); // if the checkbox has been unchecked removing the object from the array
    }
    this.getList();
  }

  /**
   * apply registerName
   *
   */
  dropdownRegister: any[] = [];
  applyRegisterName(item, event) {
    console.log(this.selectedBranch, 'selected branch');
    const obj2 = item.registersName;
    const registerName = item.registersName;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownRegisterName.push(obj2); // pushing object to newArray[]
      this.dropdownRegister.push(registerName); // pushing object to newArray[]
    } else {
      this.newDropdownRegisterName = this.newDropdownRegisterName.filter(
        (v) => v !== obj2
      ); // if the checkbox has been unchecked removing the object from the array
      this.dropdownRegister = this.dropdownRegister.filter(
        (x) => x !== registerName
      );
    }
    this.getList();
  }
  /**
   * apply registerName
   *
   */
  applyRegisterNo(item, event) {
    const obj3 = item.id;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownRegisterNo.push(obj3); // pushing object to newArray[]
    } else {
      this.newDropdownRegisterNo = this.newDropdownRegisterNo.filter(
        (v) => v !== obj3
      ); // if the checkbox has been unchecked removing the object from the array
    }
    this.getList();
  }
  /**
   * apply registerName
   *
   */
  dropDownEmployeeId: any[] = [];
  applyEmployee(item, event) {
    console.log(this.selectedBranch, 'selected branch');
    const obj4 = item.fullName;
    const employeeId = item.userId;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownEmployee.push(obj4); // pushing object to newArray[]
      this.dropDownEmployeeId.push(employeeId); // pushing object to newArray[]
    } else {
      this.newDropdownEmployee = this.newDropdownEmployee.filter(
        (v) => v !== obj4
      ); // if the checkbox has been unchecked removing the object from the array

      this.dropDownEmployeeId = this.dropDownEmployeeId.filter(
        (x) => x !== employeeId
      );
    }
    this.getList();
  }
  newDropdownPayment: any[] = [];
  applyPaymentType(item, event) {
    const obj5 = item;
    if (event.target.checked) {
      // checking if the checkbox has been checked

      this.newDropdownPayment.push(obj5); // pushing object to newArray[]
    } else {
      this.newDropdownPayment = this.newDropdownPayment.filter(
        (v) => v !== obj5
      ); // if the checkbox has been unchecked removing the object from the array
    }
    this.getList();
  }

  /**
   * change StartDate
   */
  changeStartDate(event) {
    var pipe = new DatePipe('en-US');
    this.startDate = pipe.transform(event.value);
    this.getList();
  }
  /**
   * change endDate
   */
  changeEndDate(event) {
    var pipe = new DatePipe('en-US');
    this.endDate = pipe.transform(event.value);
    this.getList();
  }
  /**
   * remove branch filter
   */
  removeBranchFilter() {
    this.newDropdownBranch = [];
    this.dropDorwnBranch = [];
    this.getList();
  }
  /**
   * remove branch filter
   */
  newDropdownRegisterName: any = [];
  removeRegisterNameFilter() {
    this.newDropdownRegisterName = [];
    this.dropdownRegister = [];
    this.register = false;
    this.getList();
  }
  /**
   * remove branch filter
   */
  newDropdownRegisterNo: any = [];
  removeRegisterNoFilter() {
    this.newDropdownRegisterNo = [];
    this.registerNo = false;
    this.getList();
  }
  /**
   * remove branch filter
   */
  newDropdownEmployee: any = [];
  removeEmployeeFilter() {
    this.newDropdownEmployee = [];
    this.dropDownEmployeeId = [];
    this.employee = false;
    this.getList();
  }
  removeCustomerFilter() {
    this.customerName = false;
  }
  onDateValueChange(event) {
    var pipe = new DatePipe('en-US');
    this.startDate = pipe.transform(event[0]);
    this.endDate = pipe.transform(event[1]);
    this.getList();
  }
  removeCollectionDateFilter() {
    this.collectionDate = false;
    (this.endDate = ''), (this.startDate = '');
    this.getList();
  }
  removePaymentFilter() {
    this.paymentType = false;
    this.newDropdownPayment = [];
    this.getList();
  }
  /**
   * resetting search input field
   */
  removeSearch(value) {
    if (value == 'name')
      this.searchText = '';
    else
      this.depositId = '';
    this.sort = 1;
    this.getList();
  }

  // Sorting Functions
  sortByBankName() {
    if (this.sort == 3) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 2 ? 3 : 2;
    }
    this.getList();
  }
  sortByRegisterName() {
    if (this.sort == 5) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 4 ? 5 : 4;
    }
    this.getList();
  }
  sortByRegisterNo() {
    if (this.sort == 7) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 6 ? 7 : 6;
    }
    this.getList();
  }
  sortByBranchName() {
    if (this.sort == 9) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 8 ? 9 : 8;
    }
    this.getList();
  }
  sortByAccountNo() {
    if (this.sort == 11) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 10 ? 11 : 10;
    }
    this.getList();
  }
  sortByDepositId() {
    if (this.sort == 13) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 12 ? 13 : 12;
    }
    this.getList();
  }
  sortByAccountName() {
    if (this.sort == 15) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 14 ? 15 : 14;
    }
    this.getList();
  }
  sortByDate() {
    if (this.sort == 17) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 16 ? 17 : 16;
    }
    this.getList();
  }
  sortByAmounts() {
    if (this.sort == 19) {
      this.sort = 1;
    } else {
      this.sort = this.sort == 18 ? 19 : 18;
    }
    this.getList();
  }

  /**
   * pagination
   * @param page page no
   */
  setPage(page: number) {
    this.pageNo = page;
    window.scroll(0, 0);
    this.getList();
  }

  /**
   * search box - get data according to the search
   */
  searchFilter(event: any, value) {
    const text = event.target.value;
    if (text.length >= 1) {
      if (value == 'name')
        this.searchText = text;
      else
        this.depositId = text;
      this.pageNo = 0;
      this.getList();
    }
    else {
      if (value == 'name')
        this.searchText = '';
      else
        this.depositId = '';
      this.getList();
    }
  }
}

